import React, {useState, useEffect} from 'react';

import {
    Button,
    Dialog, DialogContent, DialogTitle, DialogActions
} from "@material-ui/core";

import {Form} from 'react-bootstrap';

function EditCustomerDialog({active, setActive, customer_id, customer_name}) {
    const [customer, setCustomer] = useState("");

    function onSubmitCustomer(){
        fetch(process.env.REACT_APP_API+'customers/'+ customer_id +'?format=json',{
            method: 'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: JSON.stringify({
                "name": customer
            }) 
        })
    };

    useEffect(() => {
        setCustomer(customer_name);
    }, [customer_name])

    return (
        <>
            <Dialog open={active} onClose={setActive} maxWidth="xs" fullWidth>
                <DialogTitle>Изменить название общества</DialogTitle>
                <Form name="editcustomer" onSubmit={onSubmitCustomer}>
                    <DialogContent>
                        <Form.Group>
                            <Form.Control
                            type="text"
                            id="edit_customer"
                            value={customer}
                            onChange={e => setCustomer(e.target.value)} 
                            />
                        </Form.Group>
                    </DialogContent>
                <DialogActions>
                    <Button type="submit" onClick={setActive} style={{outline: "none"}}>Сохранить</Button>
                </DialogActions>
                </Form>
            </Dialog>   
        </>
    )
}

export default EditCustomerDialog;